@import url(https://fonts.googleapis.com/css?family=Ubuntu&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
    background-color: black;
}
html, body {
    margin: 0 auto;
    padding: 0 0;
    font-family: Ubuntu, sans-serif;
}

.corJumbotron {
    background-color: #59BBEC;
}

.corJumbotronServicos {
    background-color: #FFFFFF;
}

.textoVantagensContratacaoServicos {
    text-align: center;
    font-size: 24px;
}

.tamanhoTextoMedio {
    font-size: 24px;
}

a {
    color: white;
}



@media screen and (max-width: 767px){
    h1, p {
        text-align: center;
    }
}

@media screen and (max-width: 767px) {
    #blocoVantagensServicos {
        height: 210vh;
        padding: 0 0;
    }

    .parceiros .card {
        margin-top: 10px;
    }
}

/* Animações */

.cardVantagensServicos {
	position: relative;
	top: 0;
	-webkit-transition: top 1s ease;
	transition: top 1s ease;
}
.cardVantagensServicos:hover {
	top: 25px;
}

.cardServicos {
	position: relative;
	-webkit-transition: color 0.5s ease, background-color 0.5s ease, border-color 0.5s ease;
	transition: color 0.5s ease, background-color 0.5s ease, border-color 0.5s ease;
}
.cardServicos:hover {
	color: #FFFFFF;
	background-color: #59BBEC;
}


